<template>
    <div>
        <p>{{db_test}}</p>
    </div>
</template>

<script>
import { global } from '@/global'
import axios from 'axios';

var config = require("@/config.js");

export default {
  name: 'login-connect',
  data: _=> ({
        //   ,     
        //       ()
        tem_id: '',
        tem_pw: '',
        db_test: '',
    }),

  //  vue    
  created() {

    //test
    this.tem_id = global.tem_id
    this.tem_pw = global.tem_pw
    
    // this.loginConnect();
    this.getLoginInfo();
  },

    
  methods: {
    // myClass   
    // back         
    loginConnect() {
        //axios  
      var self = this
      self.$router.push({
        name: "myclass"
      })
    },
    getLoginInfo() {
      var self = this 
      axios({
        url: config.url + '/postMapping',
        crossDomain: true,
        method: 'POST',
        params: {
            userId: self.tem_id,
            userPw: self.tem_pw
        }
      }).then((result) => {
        var self = this
        var data = result.data
        if(data == "success"){
          self.$router.push({
          name: "myclass"
          })
        }
        else{
          self.db_test = data
        }
        // if (result.data.result == 200) {
        //     self.companyList = data.res;
        //     self.companyTotal = data.totalRecordSize;
        //     self.selectedCompanyList = [];
        //     if(self.inserted == true)
        //     {
        //         self.selectedCompany = self.companyList[0];
        //         self.selectedCompanyIdx = 0;
        //         self.inserted = false;
        //     }
        // }
        // else {
        //     self.snackbarMessage = '   .';
        //     self.snackbarRed = true;
        // }
      }).catch((ex) => {
        console.log("ERROR! : ", ex);
      });
    },

  }
};
</script>